<template>
  <div class="login">
    <van-nav-bar title="会员登录" left-arrow @click-left="$router.go(-1)" />
    <div class="container">
      <div class="title">
        <h3>手机号登录</h3>
        <p>未注册的手机号登录后将自动注册</p>
      </div>

      <div class="form">
        <div class="form-item">
          <input
            class="inp"
            maxlength="11"
            placeholder="请输入手机号码"
            type="text"
            v-model="mobile"
          />
        </div>
        <div class="form-item">
          <input
            class="inp"
            maxlength="5"
            placeholder="请输入图形验证码"
            type="text"
            v-model="picCode"
          />
          <img v-if="picUrl" :src="picUrl" alt="" @click="getImg()" />
        </div>
        <div class="form-item">
          <input
            class="inp"
            placeholder="请输入短信验证码"
            type="text"
            v-model="smsCode"
          />
          <button @click="getCode" ref="btn">
            {{ countDown === 60 ? "获取验证码" : countDown + "秒后重新发送" }}
          </button>
        </div>
      </div>

      <div class="login-btn" @click="login">登录</div>
    </div>
  </div>
</template>

<script>
import { getBaseImg, getMsgCodeAPI, codeLogin } from "@/api/login";
import { Toast } from "vant";
export default {
  name: "Login",
  data() {
    return {
      picUrl: "",
      picKey: "",
      countDown: 60,
      btnActive: true,
      timer: null,
      mobile: "", // 手机号
      picCode: "",
      smsCode: "",
    };
  },
  methods: {
    validFn() {
      if (!/^1[3-9]\d{9}$/.test(this.mobile)) {
        this.$toast("请输入正确的手机号");
        return false;
      }
      if (!/^\w{4}$/.test(this.picCode)) {
        this.$toast("请输入正确的图形验证码");
        return false;
      }
      return true;
    },
    async getImg() {
      const res = await getBaseImg();
      // console.log(res);
      this.picUrl = res.data.base64;
      this.picKey = res.data.key;
    },
    async getCode() {
      if (this.btnActive) {
        if (this.timer) clearInterval(this.timer);
        if (!this.validFn()) {
          return;
        }
        // 请求短信验证码
        await getMsgCodeAPI({
          captchaCode: this.picCode,
          captchaKey: this.picKey,
          mobile: this.mobile,
        });
        // console.log(res);
        Toast("发送验证码成功");

        this.btnActive = false;
        this.timer = setInterval(() => {
          this.countDown--;
          if (this.countDown === 0) {
            clearInterval(this.timer);
            this.countDown = 60;
            this.btnActive = true;
          }
        }, 1000);
      }
    },
    async login() {
      if (!this.validFn()) return;
      if (!/^\d{6}$/.test(this.smsCode)) {
        this.$toast("请输入正确的手机验证码");
        return;
      }
      const res = await codeLogin(this.mobile, this.smsCode);
      this.$toast("登录成功");
      this.$store.commit("user/setUserInfo", res.data);
      if (this.$route.query.backUrl) {
        this.$router.replace(this.$route.query.backUrl);
      } else {
        this.$router.push("/");
      }
    },
  },
  mounted() {
    this.getImg();
  },
  beforeDestroy() {
    clearInterval(this.timer);
  },
};
</script>

<style lang="less" scoped>
.container {
  padding: 49px 29px;

  .title {
    margin-bottom: 20px;
    h3 {
      font-size: 26px;
      font-weight: normal;
    }
    p {
      line-height: 40px;
      font-size: 14px;
      color: #b8b8b8;
    }
  }

  .form-item {
    border-bottom: 1px solid #f3f1f2;
    padding: 8px;
    margin-bottom: 14px;
    display: flex;
    align-items: center;
    .inp {
      display: block;
      border: none;
      outline: none;
      height: 32px;
      font-size: 14px;
      flex: 1;
    }
    img {
      width: 94px;
      height: 31px;
    }
    button {
      height: 31px;
      border: none;
      font-size: 13px;
      color: #cea26a;
      background-color: transparent;
      padding-right: 9px;
    }
  }

  .login-btn {
    width: 100%;
    height: 42px;
    margin-top: 39px;
    background: linear-gradient(90deg, #ecb53c, #ff9211);
    color: #fff;
    border-radius: 39px;
    box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.1);
    letter-spacing: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
</style>